import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';

@Component({
  selector: 'app-tpc001',
  templateUrl: './tpc001.component.html',
  styleUrls: ['./tpc001.component.scss', '../../../../../assets/cam/scss/components/tpc/_tpc001.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Tpc001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {

  tipoCuenta: Array<{
    id: string;
    titulo: string;
    caracteristicas: Array<{
          id: string;
          descripcion: string;
    }>
  }>;

  ngOnInit(): void {
    this.catalogo.data('TPC001', this.params([PAIS, MODULO, LENGUAJE])).subscribe(
      response => {
        this.tipoCuenta = response ? response : [];
      }
    );
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () =>  ({ [key: string]: any; }) {
    this.disableButton = true;
    return () => ({ id: data.id});
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }
}
