import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IAccountTypeTitle } from './card-account-type.model';

@Component({
  selector: 'app-card-account-type',
  templateUrl: './card-account-type.component.html',
  styleUrls: ['./card-account-type.component.scss']
})
export class CardAccountTypeComponent implements OnInit {

  @Input() title: string;
  @Input() id: any;
  @Output() selectOption = new EventEmitter();
  @Input() disableIf: boolean;
  constructor() { }
  ngOnInit() {}

  onSelect() {
    if ( !this.disableIf ) {
      this.selectOption.emit(this.id);
    }
  }

  get getRedTitle(): string {
    if (this.title) {
      return this.title.split(' ')[0];
    }
    return '';
  }

  get getBlackTitle(): string {
    if (this.title) {
      const redTitle = this.getRedTitle;
      const replace = redTitle ? redTitle : '';
      return this.title.replace(replace, '').trim();
    }
    return '';
  }
}
