import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Tpc001Component } from './tpc001/tpc001.component';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';

const routes: Routes = [
  {
    path: 'tpc001',
    component: Tpc001Component,
    // canActivate: [SecurityAccessGuard],
    // canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'TPC001'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TpcRoutingModule { }
